import React, {FC} from 'react'

export const OrderStatus:FC<{ status: 'created' | 'pending' | 'done' }> = ({status}) => {
    let toRussian: '' | '' | '';
    switch (status) {
        case 'created':
            toRussian = '';
            break;
        case 'pending':
            toRussian = '';
            break;
        case 'done':
            toRussian = '';
            break;
        default:
            toRussian = '';
    }
    return (
        <div
            className='text text_type_main-default'
            style={toRussian === '' ? { color: 'rgba(0, 204, 204, 1)' } : undefined}>
            {toRussian}
        </div>
    );
}
